import React from 'react'

const styles = {
  height: '100%',
  width: '150px',
  position: 'fixed',
  zIndex: '1',
  overflowX: 'hidden',
  alignItems: 'center'
}

const button = {
  border: '0',
  padding: '5px',
  background: 'transparent',
  font: 'inherit'
}

const QuestionsBar = ({ selected, numberOfQuestions, onQuestionClick }) => {
  const questionButtons = () => {
    const buttons = []
    for (let i = 1; i <= numberOfQuestions; i++)
      buttons.push(
        <li key={i}>
          <button style={button} onClick={onQuestionClick} index={i}>
            Question #{i}
          </button>
        </li>
      )
    return buttons
  }

  return (
    <div style={styles}>
      <div style={{ textAlign: 'center', margin: '10% auto' }}>
        <h1>Go To</h1>
        <ul style={{ listStyle: 'none', paddingLeft: '-10px' }}>
          {questionButtons()}
        </ul>
      </div>
    </div>
  )
}

export default QuestionsBar
