import React, { useState } from 'react'

const FlipCard = props => {
  const [flipped, setFlipped] = useState(false)

  const handleClick = () => {
    setFlipped(!flipped)
  }
  const styles = {
    flipCard: {
      perspective: '800px',
      height: '100%',
      width: '100%',
      cursor: 'pointer',
      transition: 'transform 1s',
      transformStyle: 'preserve-3d'
    },
    cardFace: {
      WebkitBackfaceVisibility: 'hidden',
      display: 'flex',
      backfaceVisibility: 'hidden',
      position: 'absolute',
      width: '100%',
      height: '100%',
      margin: 'auto',
      borderRadius: '10px',
      boxShadow:
        '0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)'
    },
    front: {
      background: 'white'
    },
    back: {
      transform: 'rotateY(180deg)',
      background: 'white'
    },
    flipped: {
      transform: 'rotateY(180deg)',
      perspective: '600px',
      height: '100%',
      width: '100%',
      cursor: 'pointer',
      transition: 'transform 1s',
      transformStyle: 'preserve-3d'
    }
  }
  return (
    <div
      style={flipped ? styles.flipped : styles.flipCard}
      onClick={handleClick}
    >
      <div style={Object.assign({}, styles.cardFace, styles.front)}>
        {props.children[0]}
      </div>
      <div style={Object.assign({}, styles.cardFace, styles.back)}>
        {props.children[1]}
      </div>
    </div>
  )
}

export default FlipCard
