import React from 'react'

const styles = {
  textAlign: 'center',
  marginTop: '20px',
  fontSize: '1.15rem'
}

const invisible = {
  visibility: 'hidden'
}

const ButtonsBar = ({ current, total, onPrevClick, onNextClick }) => {
  console.log(onNextClick)
  return (
    <div style={styles}>
      {current === 1 ? (
        <i style={invisible} className='fas fa-arrow-left'></i>
      ) : (
        <i onClick={onPrevClick} className='fas fa-arrow-left'></i>
      )}
      {'  '} {current} / {total} {'  '}
      {current === total ? (
        <i style={invisible} className='fas fa-arrow-right'></i>
      ) : (
        <i onClick={onNextClick} className='fas fa-arrow-right'></i>
      )}
    </div>
  )
}

export default ButtonsBar
