import React, { useState } from 'react'
import FlipCard from './FlipCard'
import QuestionsBar from './QuestionsBar'
import './App.css'
import questions from './questions'
import ButtonsBar from './ButtonsBar'

const styles = {
  cardFace: {
    whiteSpace: 'pre-wrap',
    backgroundColor: 'white',
    textAlign: 'justify',
    lineHeight: '28px',
    fontSize: '1rem',
    padding: '0 50px',
    margin: 'auto'
  }
}

function App() {
  const [question, setQuestion] = useState(questions[0])
  const [index, setIndex] = useState(0)
  const [frontText, setFrontText] = useState(questions[0].statement)

  const handleNextClick = event => {
    setIndex(index + 1)
    setQuestion(questions[index + 1])
  }

  const handlePrevClick = () => {
    setIndex(index - 1)
    setQuestion(questions[index - 1])
  }

  const handleQuestionClick = event => {
    let newIndex = event.target.attributes.index.value - 1
    setIndex(newIndex)
    setQuestion(questions[newIndex])
  }

  return (
    <div className='app'>
      <QuestionsBar
        numberOfQuestions={questions.length}
        selected={index + 1}
        onQuestionClick={handleQuestionClick}
      />
      <div className='card-container'>
        <FlipCard>
          <div style={styles.cardFace}>{question.statement}</div>
          <div style={styles.cardFace}>{question.answer}</div>
        </FlipCard>
        <ButtonsBar
          current={index + 1}
          total={questions.length}
          onNextClick={handleNextClick}
          onPrevClick={handlePrevClick}
        />
      </div>
    </div>
  )
}

export default App
